
import { defineComponent } from 'vue'
import { COM20400311_REQ_BODY, COM20400311_RES, ScreenInfo } from '@/shared/type/COM20400311.type'
import { COM21100113_RES } from '@/shared/type/APM20100131.type'
import { COMMON_KEY_VALUE, PAGE_SIZE } from '@/shared/constants/common.const'
import Pagination from '@/shared/components/Pagination/Pagination.vue'
import BreadCrumb from '@/shared/components/BreadCrumb/BreadCrumb.vue';
import SelectOption from '@/shared/components/SelectOption/SelectOption.vue';
// import SelectOptionTemp from "@/shared/components/SelectOptionTemp/SelectOptionTemp.vue"
export default defineComponent({
    name: "com204003sc00Vue",
    components: {
        Pagination,
        BreadCrumb,
        SelectOption,
        // SelectOptionTemp
    },
    data() {
        const header = [
            { columnName: "No" },
            { columnName: "Screen ID" },
            { columnName: "Screen Name" },
            { columnName: "Screen Type Code" },
            { columnName: "Business Category Code" },
            { columnName: "Screen Status Code" }
        ];
        return {
            COMMON_KEY_VALUE,
            header,
            baseScreenList: [] as ScreenInfo[],
            screenList: [] as ScreenInfo[],
            isReset: false,
            searchData: {
                screenID: "",
                screenName: "",
                businessCategoryCode: "",
                screenStatusCode: "",
                searchValue: ""
            },
            totalItems: 0,
            currentPage: 1,
            pageSize: PAGE_SIZE,
            startingIndex: 1,
            businessCategoryItems: [] as any,
        }
    },
    mounted() {
        this.onGetScreenInfoList();
    },
    computed: {
        getScreenStatusCode(): any {
            const statusCode = [] as any;
            if (this.$store.state.CommonCodeModule.commonCode.ScreenStatusCode) {
                this.$store.state.CommonCodeModule.commonCode.ScreenStatusCode.find(item => {
                    if (item.value != 'Deleted') {
                        statusCode.push(item)
                    }
                })
            }
            return statusCode
        }
    },
    methods: {
        async onGetScreenInfoList() {
            const reqBody: COM20400311_REQ_BODY = {
                pageNumber: this.currentPage,
                pageSize: this.pageSize,
                businessCategoryCode: this.searchData.businessCategoryCode,
                searchValue: this.searchData.searchValue,
                screenStatusCode: this.searchData.screenStatusCode
            }
            const responseData = await this.$request.getRequest('COM20400311', reqBody, false) as COM20400311_RES
            if (responseData.header.result) {
                const lists = responseData.body.list;
                const addedListKey = this.onMappingTableData(lists);
                this.baseScreenList = addedListKey
                this.screenList = addedListKey
                this.totalItems = responseData.body.totalCount
            }
        },
        onClickRefresh() {
            this.pageSize = PAGE_SIZE;
            this.searchData = {
                searchValue: "",
                screenName: "",
                screenID: "",
                businessCategoryCode: "",
                screenStatusCode: ""
            }
            this.currentPage = 1;
            this.isReset = true;
            this.onGetScreenInfoList();
        },
        onFilter(filterData, key) {
            return filterData.filter((item: any) => item[key].toLowerCase().includes(this.searchData[key].toLowerCase()))
        },
        async onClickSearch() {
            if (this.searchData.searchValue == '' && this.searchData.screenStatusCode == '' && this.searchData.businessCategoryCode == '') {
                this.isReset = true;
                this.currentPage = 1;
                this.pageSize = PAGE_SIZE;
                this.onGetScreenInfoList();
            } else {
                this.currentPage = 1;
                this.isReset = true
                this.onGetScreenInfoList();
            }
        },
        handleRegister() {
            this.$router.push({
                path: '/COM204003SC01',
                query: {
                    type: 'register'
                }
            })
        },
        handleRowClick(rowData) {
            if (!rowData) return
            this.$router.push({
                path: '/COM204003SC01',
                query: {
                    type: 'edit',
                    screenID: rowData
                }
            })
        },

        handlePagination(pageNumber: number) {
            this.currentPage = pageNumber
            this.isReset = false;
            this.onGetScreenInfoList();
        },

        handleStartingIndex(startingIndex: number) {
            this.startingIndex = startingIndex
        },
        onMappingTableData(lists) {
            const { BusinessCategoryCode, ScreenStatusCode, ScreenTypeCode } = this.$store.state.CommonCodeModule.commonCode;
            return lists.map((item, index) => ({
                No: index + 1,
                screenID: item.screenID,
                screenName: item.screenName,
                screenTypeCode: ScreenTypeCode ? `${ScreenTypeCode.find((type) => type.key === item.screenTypeCode)?.key}: ${ScreenTypeCode.find((type) => type.key === item.screenTypeCode)?.value}` : '',
                businessCategoryCode: BusinessCategoryCode ? `${BusinessCategoryCode.find((category) => category.key === item.businessCategoryCode)?.key}: ${BusinessCategoryCode.find((category) => category.key === item.businessCategoryCode)?.value}` : '',
                screenStatusCode: ScreenStatusCode ? `${ScreenStatusCode.find((status) => status.key === item.screenStatusCode)?.key}: ${ScreenStatusCode.find((status) => status.key === item.screenStatusCode)?.value}` : '',
            }));
        },

        async getBussinessCatgoryCode() {
            const responseData = await this.$request.getRequest('COM21100113', { businessCommonStatusCode: "01", languageCode: "", standardCodeDomainName: "BusinessCategoryCode", standardCodeValue: "" }, false) as COM21100113_RES
            this.businessCategoryItems = responseData.body.list;
        },

        statusConverter(statusCode) {
            switch (statusCode) {
                case '01: Active':
                    return 'success'
                case '02: Inactive':
                    return 'warning'
                default:
                    return "09: fail"
            }
        }
    }
})
